.navbar {
    width: 100%;
    height: 60px;
    background-color: #510000;
    display: flex;
    flex-direction: row;
}

.navbar .leftSide {
    flex: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbar .brand {
    flex: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-left: 50px;
}

.navbar .leftSide img {
    width: 50px;
}

.navbar .rightSide {
    flex: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 40px;
}

.navbar a {
    color: white;
    text-decoration: none;
    margin: 20px;
    font-size: large;
}

.navbar .rightSide button {
    background-color: transparent;
    border: none;
    color: white;
    cursor: pointer;
}

.navbar .rightSide svg {
    font-size: 40px;
}

.navbar #open {
    padding-left: 0px;
    height: 60px;
}
.navbar #open .brand {
    display: none;
}

.navbar #close .brand {
    display: inherit;
}

.navbar #open .hiddenLinks {
    display: inherit;
    margin-left: 5px;
}

.navbar #close .hiddenLinks {
    display: none;
}

.navbar #open a {
    width: 30px;
    justify-content: center;
}

@media only screen and (max-width: 900px) {
    .navbar .rightSide a {
        width: 70px;
    }
    .navbar .leftSide {
        padding-left: 10px;
    }
}

@media only screen and (max-width: 600px) {
    .navbar .rightSide a {
        display: none;
    }

    .navbar .rightSide {
        justify-content: flex-end;
        padding-right: 15px;
    }

    .navbar .rightSide button {
        display: inherit;
    }
}

@media only screen and (min-width: 600px) {
    .navbar .rightSide button {
        display: none;
    }

    .hiddenLinks {
        display: none;
    }
}